$color-primary: #c69963;
$color-primary-dark: #b28451;

$color-secondary: #101d2c;
.infopage {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: $color-secondary;
}
.info-image {
  position: absolute;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  height: 15rem;
  width: 15rem;
}
.swap-enter {
  transform: translateX(100%);
}
.swap-enter-active {
  transform: translateX(0%);
  transition: transform 900ms;
}

.swap-exit {
  transform: translateX(0%); //0
}
.swap-exit-active {
  transform: translateX(-100%); //100
  transition: transform 900ms;
}
.swap-exit-done {
  display: none;
}
.info-input-error {
  color: red;
  text-align: right;
}
.info-input-name {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  width: 25rem;
  height: 3.5rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  outline: none;
  font-family: inherit;
  text-transform: capitalize;
  &:focus {
    background-color: $color-primary;
  }
}
.info-input-email {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  width: 25rem;
  height: 3.5rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  outline: none;
  font-family: inherit;
  background-color: #fff;

  &:focus {
    background-color: $color-primary;
  }
}
.phone {
  display: flex;
  flex-direction: row;
}
.info-input-phonecode {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  width: 7rem;
  height: 3.5rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  outline: none;
  font-family: inherit;
}

.info-input-phone {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  width: 18rem;
  height: 3.5rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  outline: none;
  font-family: inherit;
  &:focus {
    background-color: $color-primary;
  }
}
.info-select {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  width: 25rem;
  height: 3.5rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  font-family: inherit;
}
.next-button {
  right: 3rem;
  bottom: 3rem;
  position: absolute;
  padding: 1rem;
  height: min-content;
  width: 25rem;
  align-self: flex-end;
  background-color: $color-secondary;
  color: white;
  font-size: 2rem;
  text-align: center;
  outline: none;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  &:active {
    transform: translateY(0.2rem);
  }
  &:disabled {
    background-color: #566575;
    cursor: not-allowed;
  }
}
.back-button {
  left: 3rem;
  bottom: 3rem;
  position: absolute;
  padding: 1rem;
  height: min-content;
  width: 25rem;
  align-self: flex-end;
  background-color: $color-secondary;
  color: white;
  font-size: 2rem;
  text-align: center;
  outline: none;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  &:active {
    transform: translateY(0.2rem);
  }
}

@media (max-width: 600px) {
  .infopage {
    flex-direction: column;
    justify-content: center;
  }
  .next-button {
    width: 20rem;
  }
  .back-button {
    width: 20rem;
  }
}
