$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
//fugaz one
.header {
  width: 100%;
  height: 52rem;
  display: flex;

  .main_section {
    height: 100%;
    width: 80%;
    background-image: linear-gradient(
        rgba(#101d2c96, 0.9),
        rgba(#101d2c80, 0.9)
      ),
      url(../../assets/dubai-wave-hotel.jpg);
    background-size: cover;
    background-position: center;
    padding: 2rem;
  }
  .logo {
    position: relative;
    top: 5%;
    left: 80%;
    height: 12rem;
    width: 12rem;
  }
  .main_section-container {
    position: relative;
    top: 20%;
    .title {
      margin: 2rem;
      font-size: 5rem;
      color: $color-primary;
    }
    .line {
      margin: 2rem;
      color: white;
      font-size: 3rem;
    }
    .button {
      margin: 2rem;
      background-color: $color-primary;
      color: white;
      border: none;
      outline: none;
      width: 20rem;
      height: 5rem;
      font-size: 1.8rem;
      border-radius: 0.2rem;
      cursor: pointer;
      &:active {
        transform: translateY(0.2rem);
      }
      &:hover {
        background-color: $color-primary-dark;
      }
    }
  }

  .ceo_section {
    height: 100%;
    width: 30%;
    padding: 2rem;
    background-color: #101d2c;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .ceo_section-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;

    .ceo_img {
      margin: 2rem;
      height: 17rem;
      width: 17rem;
      border-radius: 50%;
    }
    .text {
      margin: 1rem;
      font-size: 1.6rem;
      text-align: center;
      color: white;
    }
    .name {
      text-align: center;
      font-size: 1.4rem;
      color: $color-primary;
    }
  }
}
@media (max-width: 600px) {
  .header {
    flex-direction: column;
    height: min-content;
    min-height: min-content;

    .main_section {
      width: 100%;
    }
    .ceo_section {
      display: none;
      //   width: 100%;
      //   display: flex;
      //   flex-direction: row;
    }
    // .ceo_section-container {
    //   width: 60%;
    // }
  }
}
