$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
.rooms {
  grid-column: page-start / page-end;
  padding: 2rem;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-around;
  .room {
    margin: 2rem;
    display: flex;
    border-radius: 0 0 0.3rem 0.3rem;
    box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25),
      0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
    background-color: rgb(214, 214, 214);
    align-content: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    .room_img {
      height: 20rem;
      width: 34rem;
      border-radius: 0.3rem 0.3rem 0 0;
    }
    .info {
      background-color: rgb(214, 214, 214);
      padding-top: 2.5rem;
      height: 20rem;
      width: 34rem;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: center;
      align-content: space-around;
      align-items: center;
      .icon {
        margin: 2rem;
        color: $color-primary-dark;
        color: $color-secondary;
        font-size: 3rem;
      }
      .icon_container {
        .icon {
          margin-left: 1rem;
          margin-right: 1rem;
        }
      }
    }
    .button {
      background-color: $color-primary;
      color: white;
      border: none;
      outline: none;
      width: 34rem;
      height: 5rem;
      line-height: 5rem;
      font-size: 1.9rem;
      border-radius: 0 0 0.3rem 0.3rem;
      text-align: center;
      text-shadow: 0px 3px 3px rgba(150, 150, 150, 1);
      cursor: pointer;
      &:active {
        transform: translateY(0.2rem);
      }
      &:hover {
        background-color: $color-primary-dark;
      }
    }
    .title {
      border-radius: 0.3rem;
      line-height: 5rem;
      position: absolute;
      height: 5rem;
      width: 24rem;
      top: 17.5rem;
      left: 5rem;
      color: white;
      font-size: 2rem;
      text-align: center;
      background-color: $color-secondary;
    }
  }
}
@media (max-width: 1024px) {
  .rooms {
    grid-column: page-start / page-end;
    padding: 1rem;

    .room {
      margin: 2rem;

      .room_img {
        height: 15rem;
        width: 25rem;
        border-radius: 0.3rem 0.3rem 0 0;
      }
      .info {
        padding-top: 2rem;
        height: 15rem;
        width: 25rem;

        .icon {
          margin: 1.2rem;
          font-size: 2.2rem;
        }
        .icon_container {
          .icon {
            margin-left: 0.5rem;
            margin-right: 0.5rem;
          }
        }
      }
      .button {
        width: 25rem;
        height: 3rem;
        line-height: 2.5rem;
        font-size: 1.2rem;
      }
      .title {
        border-radius: 0.3rem;
        line-height: 2.5rem;

        height: 4rem;
        width: 18rem;
        top: 13rem;
        left: 4rem;
        color: white;
        font-size: 1.2rem;
        line-height: 4rem;
      }
    }
  }
}
