$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
$color-secondary-light: #213f61;
.confirmpage {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 5rem;
}
.confirm-text {
  width: 50%;
  text-align: center;
  font-size: 2rem;
  color: #101d2c;
  margin: 2rem;
  margin-bottom: 4rem;
}
.confirm-info {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
  font-size: 2rem;
  color: #101d2c;
}
.confirm-info-data {
  text-align: left;
}
.confirm-info-name {
  font-size: 2.2rem;
  text-transform: capitalize;
}
.confirm-info-email {
  font-size: 2.2rem;
}
.confirm-info-phone {
  font-size: 2.2rem;
}
.confirm-image {
  position: absolute;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  height: 15rem;
  width: 15rem;
}
.swap-enter {
  transform: translateX(100%);
}
.swap-enter-active {
  transform: translateX(0%);
  transition: transform 900ms;
}

.swap-exit {
  transform: translateX(0%); //0
}
.swap-exit-active {
  transform: translateX(-100%); //100
  transition: transform 900ms;
}
.swap-exit-done {
  display: none;
}
