$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
.footer {
  grid-column: page-start/page-end;
  background-color: $color-secondary;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  padding: 1rem;
}
.footer-logo {
  width: 15rem;
  height: 15rem;
}

.footer-section {
  padding: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
h2 {
  color: $color-primary;
  margin-bottom: 1rem;
}
.footer-about {
  width: 50%;
  color: white;
  font-size: 1.4rem;
}
.footer-contact {
  width: 30%;
  color: white;
  font-size: 1.4rem;
}
.footer-icons {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer-icon {
  font-size: 3rem;
  color: white;
}

@media (max-width: 1024px) {
  .footer {
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .footer-section {
    padding: 3rem;
    flex-direction: column;
    width: 100%;
  }
  .footer-icons {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-around;
  }
  .footer-icon {
    font-size: 5rem;
  }
  .footer-about {
    margin-top: 2rem;
    margin-bottom: 2rem;
    width: 100%;
    font-size: 2rem;
  }
  .footer-contact {
    width: 100%;
    color: white;
    font-size: 2rem;
  }
  .footer-logo {
    position: absolute;
    right: 2rem;
    bottom: 2rem;
  }
}
