$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
$color-secondary-light: #213f61;
// first page of booking translates to info page
.bookpage {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
// calendar and text below it
.bookpage-datepicker {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 30%;
}
.bookpage-datepicker-text {
  text-align: center;
  margin: 1rem;
  font-size: 1.5rem;
  color: $color-secondary;
}
// room picking part
.bookpage-roomselect {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 35%;
  max-height: 80rem;
}
.bookpage-roomselect-title {
  text-align: center;
  font-size: 3rem;
  color: $color-secondary;
}
.bookpage-roomselect-options {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 30%;
  height: 100%;
  padding: 2rem;
  padding-left: 4rem;
  align-content: center;
}
.bookpage-room {
  padding-left: 0.5rem;
}
.bookpage-room-price {
  font-size: 1.5rem;
}
// cruise options
.bookpage-cruiseselect {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 25%;
  max-height: 80rem;
}
.bookpage-cruise-title {
  text-align: center;
  font-size: 3rem;
  color: $color-secondary;
}
.bookpage-cruiseselect-options {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
  height: 70%;
  padding: 2rem;
  align-content: center;
}
.bookpage-cruiseselect-option {
  margin: 3rem;
  font-size: 2rem;
}
.bookpage-cruiseselect-free {
  text-align: center;
  font-size: 2rem;
  color: $color-secondary;
}
.bookpage-cruiseselect-special {
  text-align: center;
  font-size: 1.5rem;
  color: $color-primary-dark;
}
.next-button {
  right: 3rem;
  bottom: 3rem;
  position: absolute;
  padding: 1rem;
  height: min-content;
  width: 25rem;
  align-self: flex-end;
  background-color: $color-secondary;
  color: white;
  font-size: 2rem;
  text-align: center;
  outline: none;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  &:active {
    transform: translateY(0.2rem);
  }
  &:disabled {
    background-color: #566575;
    cursor: not-allowed;
  }
}
.total {
  left: 3rem;
  bottom: 3rem;
  position: absolute;
  font-size: 2.5rem;
  color: $color-secondary;
}
///////////////////////////////////////////////////////////////////////
@media (max-width: 1024px) {
  .bookpage-datepicker {
    width: 40%;
  }
  .bookpage-cruiseselect {
    width: 30%;
  }
  .bookpage-roomselect {
    width: 30%;
  }
}
@media (max-width: 800px) {
  .bookpage-datepicker {
    width: 45%;
  }
  .bookpage-cruiseselect {
    width: 30%;
  }
  .bookpage-roomselect {
    width: 25%;
  }
}
@media (max-width: 600px) {
  .bookpage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .bookpage-datepicker {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 60%;
    width: 100%;
  }
  .calendar {
    font-size: 0.8rem;
    width: 40rem;
    height: 40rem;
  }
  .bookpage-roomselect-title {
    width: 100%;
  }
  .bookpage-roomselect-options {
    width: 100%;
    display: flex;
    flex-direction: row;
  }
  .bookpage-roomselect {
    height: 20%;
    width: 100%;
    display: flex;
  }
  .bookpage-cruiseselect {
    height: 20%;
    width: 100%;
    display: flex;
  }
  .bookpage-cruiseselect-options {
    flex-direction: row;
    justify-content: center;
    padding: 0;
  }
  .bookpage-cruiseselect-free {
    display: none;
  }
  .bookpage-cruiseselect-special {
    display: none;
    width: 40%;
    font-size: 1rem;
  }
  .next-button {
    width: 20rem;
  }
}

// css transition animation
.swap-enter {
  transform: translateX(100%);
}
.swap-enter-active {
  transform: translateX(0%);
  transition: transform 900ms;
}

.swap-exit {
  transform: translateX(0%); //0
}
.swap-exit-active {
  transform: translateX(-100%); //100
  transition: transform 900ms;
}
.swap-exit-done {
  display: none;
}
