.amenities {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 2rem;
  justify-content: space-around;
  width: 100%;
}
.amenitie {
  width: 30%;
  margin: 2rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;

  .title {
    top: 50%;
    font-size: 2rem;
    display: flex;
    justify-content: space-between;
    text-align: center;

    .icon {
      color: #b28451;
      font-size: 5rem;
      height: 5rem;
      width: 5rem;
    }
  }
  .text {
    font-size: 1.6rem;
    text-align: justify;
  }
}
@media (max-width: 1024px) {
  .amenities {
    padding: 2rem;
    flex-direction: row;
    justify-content: center;
    align-content: center;
  }
  .amenitie {
    margin: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    width: 40%;
  }
  .amenitie-icon {
    color: #b28451;
    font-size: 10rem;
    height: 5rem;
    width: 5rem;
    svg {
      font-size: 5rem;
      height: 5rem;
      width: 5rem;
    }
  }
  .amenitie-text {
    font-size: 2rem;
  }
}
