$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
.cruise {
  grid-column: page-start/page-end;
  display: flex;
  justify-content: space-between;

  .cruise-img {
    height: 40rem;
    width: 65rem;
  }
  .main_section {
    margin: 2rem;
    padding: 1.5rem;
    .text {
      margin: 2rem;
      font-size: 2rem;
      color: $color-secondary;
    }
    .title {
      margin: 2rem;
      font-size: 3rem;
      color: $color-primary-dark;
    }
    .button {
      margin: 2rem;
      background-color: $color-primary;
      color: white;
      border: none;
      outline: none;
      width: 20rem;
      height: 5rem;
      font-size: 1.8rem;
      border-radius: 0.2rem;
      cursor: pointer;
      &:active {
        transform: translateY(0.2rem);
      }
      &:hover {
        background-color: $color-primary-dark;
      }
    }
  }
}
@media (max-width: 1024px) {
  .cruise {
    display: flex;
    flex-direction: column;

    .cruise-img {
      height: 25rem;
      width: 35rem;
      align-self: flex-end;
    }
  }
}

