$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
.modal {
  background-color: white;
  border: double $color-primary-dark 1rem;
  border-radius: 0.5rem;
  width: 85%;
  height: 82%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 6;
  display: flex;
  overflow: hidden;
  .modalClose {
    position: absolute;
    font-size: 3rem;
    color: $color-secondary;
    top: 0.8rem;
    right: 0.8rem;
    cursor: pointer;
    z-index: 100;
  }
}
@media (max-width: 650px) {
  .modal {
    height: 97%;
    width: 95%;
  }
}
