$color-primary: #c69963;
$color-primary-dark: #b28451;

$color-secondary: #101d2c;
.successful {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: $color-secondary;
}
