$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
.location {
  grid-column: page-start / page-end;
  height: 50rem;
  display: flex;
}
.location-images {
  width: 67%;
  height: 100%;
  background-color: $color-secondary;
  display: grid;
  row-gap: 1.5rem;
  column-gap: 1.5rem;
  padding: 1.5rem;
  align-content: center;
  align-items: center;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.image-kalemegdan {
  border-radius: 0.3rem;
  grid-column: 3/5;
  grid-row: 4/6;
  height: 100%;
  object-fit: cover;

  width: 100%;
}
.image-skadarlija {
  border-radius: 0.3rem;
  grid-column: 1/3;
  grid-row: 4/6;
  width: 100%;
  height: 100%;
}
.image-pobednik {
  border-radius: 0.3rem;
  grid-column: 5/6;
  grid-row: 3/6;
  height: 100%;
  width: 100%;
}
.image-bw {
  border-radius: 0.3rem;
  grid-column: 1/4;
  grid-row: 1/4;
  height: 100%;
  width: 100%;
}
.image-trg {
  border-radius: 0.3rem;
  grid-column: 4/6;
  grid-row: 1/3;
  height: 100%;
  width: 100%;
}
.image-museum {
  border-radius: 0.3rem;
  grid-row: 3/4;
  grid-column: 4/5;
  height: 100%;
  width: 100%;
}
.location-quote {
  background-color: $color-secondary;
  width: 33%;
  height: 100%;
}
.location-quote-container {
  padding-left: 3rem;
  position: relative;
  top: 25%;
  .location-quote-title {
    margin: 2rem;
    font-size: 1.8rem;
    color: $color-primary;
  }
  .location-quote-line {
    margin: 2rem;
    color: white;
    font-size: 2.2rem;
  }
  .location-quote-button {
    margin: 2rem;
    background-color: $color-primary;
    color: white;
    border: none;
    outline: none;
    width: 20rem;
    height: 5rem;
    font-size: 1.8rem;
    border-radius: 0.2rem;
    cursor: pointer;
    &:active {
      transform: translateY(0.2rem);
    }
    &:hover {
      background-color: $color-primary-dark;
    }
  }
}
@media (max-width: 1024px) {
  .location {
    flex-direction: column;
    height: min-content;
  }
  .location-images {
    width: 100%;
    height: 45rem;
  }
  .location-quote {
    width: 100%;
    height: min-content;
  }
}

@media (max-width: 600px) {
  .location-images {
    height: 35rem;
  }
}
